/* Edited for Horizon Theme */

.overview-shipping-header {
  display: inline-block;
}
.overview-shipping-header h4 {
  margin: $sc-margin-lv2 0;
  color: $sc-color-senary;
}
.overview-shipping-card {
  @extend .info-card;
  width: 100%;
  border: none;
  padding: $sc-padding-lv2 0;
}
.overview-shipping-card .address-details {
  border: 0;
  padding: 0;
}
.overview-shipping-card-content {
	@media (min-width: $screen-sm-min) {
		min-height: 200px;
        overflow: auto;
	}
}
.overview-shipping-card-button-edit {
  @extend .button-edit;
  margin-top: $sc-margin-lv3;
}
